import { Component, OnInit, ElementRef, ViewChild, AfterViewInit } from '@angular/core';
import { Permission } from 'src/app/models/permission.model';
import { NgbModalRef, NgbModal, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';
import { IResourceDetailsContext } from 'src/app/shared/dashboard-resource/resource-details/resource-details.context';
import { PermissionsResolver } from '../permissions.resolver';

@Component({
  selector: 'app-permission-details',
  templateUrl: './permission-details.component.html',
  styleUrls: ['./permission-details.component.scss']
})
export class PermissionDetailsComponent implements OnInit {
  public context: IResourceDetailsContext<Permission>;

  ngOnInit(): void {
    const permission: Permission = PermissionsResolver.Json2Permission(history.state.resource);

    this.context = {
      urls: {
        tableUrl: '/permissions',
      },
      data: permission,
      properties: [
        {
          key: 'name',
          title: 'Name',
          type: 'string'
        },
        {
          key: 'description',
          title: 'Description',
          type: 'string'
        },
        {
          key: 'created_at',
          title: 'Created At',
          type: 'datetime'
        },
        {
          key: 'updated_at',
          title: 'Updated At',
          type: 'datetime'
        },
      ],
      title: `Details of permission ${ permission?.name }`
    };
  }
}
